import React, { Component } from 'react';
import { connect } from 'react-redux';
import { fetchGeneration } from '../actions/generation';
import fetchStates from '../reducers/fetchStates';

const MINIMUM_DELAY = 3000;

class Generation extends Component {

    timer = null;

    componentDidMount() {
        this.fetchNextGeneration();
    }

    componentWillUnmount() {
        clearTimeout(this.timer)
    }

    fetchNextGeneration = () => {
        this.props.fetchGeneration();

        let delay = new Date(this.props.generation.expiration).getTime() - new Date().getTime();

        if (delay < MINIMUM_DELAY) {
            delay = MINIMUM_DELAY;
        }

        this.timer = setTimeout(() => this.fetchNextGeneration(), delay);
    }

    render() {
        const { generation } = this.props;

        if (generation.status === fetchStates.error) {
            return <div>{generation.message}</div>;
        }

        return (
            <div className="generationContainer">
                <h3>Generation {generation.generationId}</h3>
                <h4>Expires on: </h4>
                <p>{new Date(generation.expiration).toString()}</p>
            </div>
        )
    }
}

const mapStateToProps = (state) => {
    const generation = state.generation;
    return { generation }
};

// const mapDispatchToProps = (dispatch) => {
//     return {
//         fetchGeneration: () => fetchGeneration(dispatch)
//     };
// };

const componentConnector = connect(
    mapStateToProps,
    { fetchGeneration }
);

export default componentConnector(Generation);